import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import LineGraph from '../../components/lineGraph';

const MONTHS = {
    no: ['jan', 'feb', 'mar', 'apr', 'mai', 'jun', 'jul', 'aug', 'sep', 'okt', 'nov', 'des'],
    en: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
};


@withText((props) => ({
    week: <Text id='workout.graph.week' />,
    distance: <Text id='workout.graph.distance' />,
    ascend: <Text id='workout.graph.ascend' />,
}))
@observer
class WorkoutGraph extends Component {
    render() {
        const { userStore } = this.props.stores;
        const { isLoading, week, distance, ascend } = this.props;
        const {
            user,
			workoutSummary,
			graphWorkoutSummaryWeekDistanceCurrent, graphWorkoutSummaryWeekDistancePrev,
			graphWorkoutSummaryWeekElevationCurrent, graphWorkoutSummaryWeekElevationPrev,
		} = userStore;
        console.log('graphWorkoutSummaryWeekDistanceCurrent', graphWorkoutSummaryWeekDistanceCurrent);
        const { language = 'no' } = user;

        return (
            <>
                <h5><Text id='workout.graph.title'>Treningsstatistikk</Text></h5>
                <div class='w-100 mt-3 mb-4'>
                    <small class='mb-4'><small>
                        <table class='table table-sm font-weigth-lighter border-bottom'>
                            <thead>
                                <tr>
                                    <th style='padding: .2rem;'><Text id='workout.graph.month'>Mnd</Text></th>
                                    {util.range(1, 12).map(m => (
                                        <th class='text-right' style='padding: .2rem;'><a href={`/workouts/month/${m}`}>{MONTHS[language][m - 1]}</a></th>
                                    ))}
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <th scope='row' style='padding: .2rem;'>2019</th>
                                    {util.range(1, 12).map(m => (
                                        <>
                                            <td class='text-right' style='padding: .2rem;'>
                                                <nobr>{userStore.findWorkoutSummaryMonth(2019, m)}</nobr><br />
                                                <nobr>{userStore.findWorkoutSummaryMonth(2019, m, 'elevation')}</nobr><br />
                                            </td>
                                        </>
                                    ))}
                                    <td class='text-muted' style='padding: .2rem;'>
                                        <nobr><i class='fas fa-road' /> km</nobr><br />
                                        <nobr><i class='fas fa-mountain' /> m</nobr><br />
                                    </td>
                                </tr>
                                <tr>
                                    <th scope='row' style='padding: .2rem;'>2020</th>
                                    {util.range(1, 12).map(m => (
                                        <>
                                            <td class='text-right' style='padding: .2rem;'>
                                                <nobr>{userStore.findWorkoutSummaryMonth(2020, m)}</nobr><br />
                                                <nobr>{userStore.findWorkoutSummaryMonth(2020, m, 'elevation')}</nobr><br />
                                            </td>
                                        </>
                                    ))}
                                    <td class='text-muted' style='padding: .2rem;'>
                                        <nobr><i class='fas fa-road' /> km</nobr><br />
                                        <nobr><i class='fas fa-mountain' /> m</nobr><br />
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </small></small>

                    <LineGraph
                        stores={this.props.stores}
                        width={500}
                        height={150}
                        showXTicks={1}
                        xTicksVal={val => `${week} ${val}`}
                        paddingTop={30}
                        lineFromXzero={true}
                        lineFromXmax={true}
                        xTicksLink={'/workouts/week/'}

                        dataLeft={[graphWorkoutSummaryWeekDistanceCurrent, graphWorkoutSummaryWeekDistancePrev]}
                        legendLeft={[`2020 ${distance}`, `2019 ${distance}`]}
                        // yMaxLeft={30}
                        // yMinLeft={0}
                        showYTicksLeft={1}
                        yTicksLeftPostfix={'km'}
                        unitLeft={['km', 'km']}
                        avgWindowLeft={[0, 0]}
                        smoothLeft={[0, 0]}

                        dataRight={[graphWorkoutSummaryWeekElevationCurrent, graphWorkoutSummaryWeekElevationPrev]}
                        legendRight={[`2020 ${ascend}`, `2019 ${ascend}`]}
                        showYTicksRight={1}
                        yTicksRightPostfix={'m'}
                        unitRight={['m', 'm']}
                        avgWindowRight={[0, 0]}
                        smoothRight={[0, 0]}

                        // marker={week}
                    />
                </div>

                {!isLoading && workoutSummary && workoutSummary.length < 3 && (
                    <div class='mb-5 py-3 border-top border-bottom'>
                        <div class='text-left' style='line-height: 1.0em;'>
                            <div class='display-4 float-left text-warning mr-3'>
                                <i class='fas fa-paw' />
                            </div>
                            <small>
                                <Text id='workout.graph.too-little-data'>Foreløpig er det litt lite data for å kunne vise noen fine grafer. 
                                Etterhvert som du legger inn flere treninger så vil dette bli en fin tabell og graf som viser progresjonen din.</Text>
                            </small>
                        </div>
                    </div>
                )}
            </>
        );
    }
}

export default WorkoutGraph;
