import { h, Component } from 'preact';
import { Link } from 'preact-router/match';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import linkState from 'linkstate';
import { route } from 'preact-router';
import { Text, Localizer } from 'preact-i18n';

import style from './style.css';


const initialState = {
    menu: {
		profile: false,
		main: false,
	},
	searchText: '',
};

@observer
class Navigation extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	toggleDropdown = (e) => {
		const { menu } = this.state;
		let menuName = e.target.dataset.name;
		if (!menuName && e.target.parentNode) {
			menuName = e.target.parentNode.dataset.name;
		}
		if (menuName) {
			menu[menuName] = !menu[menuName];
		}
		this.setState({ menu });
	}

	toggleDarkmode = (e) => {
        const { appState } = this.props.stores;
		appState.toggleDarkmode();
		this.toggleDropdown(e);
	}

	adminLoadAll = async (e) => {
        const { userStore } = this.props.stores;
        await userStore.getInfo(true);
	}

	simulateUser = (e) => {
		let email = e.target.dataset.email;
        const { userStore } = this.props.stores;
		userStore.getSimulateUser(email);
		this.toggleDropdown(e);
	}

	changeLanguage = (e) => {
		const lang = e.target.value;
		const { userStore } = this.props.stores;
		const { user } = userStore;
		userStore.setLanguage({ email: user.email, language: lang });
	}

	search = () => {
		const { searchText } = this.state;
		route(`/search/${searchText}`, true);
	}

    render() {
		const { menu, searchText } = this.state;
        const { appState, userStore } = this.props.stores;
        const { darkmode } = appState;
		const { allUsers, isAdmin, user } = userStore;
		const { language = 'no' } = user;

		return (
			<nav class='navbar navbar-expand-lg navbar-themed fixed-top'>
				<a class='navbar-brand' href='/'><i class='fas fa-paw' /> The Musher</a>
				<ul class='navbar-nav ml-auto'>
					<li class='nav-item mr-3'>

						<div class='form-row align-items-center'>
							<div class='col-auto my-1'>
								<select class='custom-select'
									style='background-color: inherit; border: none; padding: 0;'
									onInput={this.changeLanguage}
								>
									<option value='no' selected={language === 'no'}>no</option>
									<option value='en' selected={language === 'en'}>en</option>
								</select>
							</div>
						</div>
					</li>
				</ul>
				<button class='navbar-toggler' type='button'
					data-name='main'
					onClick={this.toggleDropdown}
				>
					<span class='navbar-toggler-icon' />
				</button>

				<div class={`collapse navbar-collapse ${menu.main ? 'show' : ''}`} id='navbarSupportedContent'>
					<ul class='navbar-nav mr-auto'>
						<li class='nav-item'>
							<Link className='nav-link' activeClassName={'active'} href='/dogs'
								data-name='main'
								onClick={this.toggleDropdown}
							><i class='fas fa-dog' /> <Text id='header.dogs'>Hundene</Text></Link>
						</li>
						<li class='nav-item'>
							<Link className='nav-link' activeClassName={'active'} href='/workouts'
								data-name='main'
								onClick={this.toggleDropdown}
							><i class='fas fa-running' /> <Text id='header.workouts'>Treningen</Text></Link>
						</li>
						<li class='nav-item'>
							<Link className='nav-link' activeClassName={'active'} href='/tracks'
								data-name='main'
								onClick={this.toggleDropdown}
							><i class='fas fa-map-marked' /> <Text id='header.tracks'>Rundene</Text></Link>
						</li>
						<li class='nav-item'>
							<Link className='nav-link' activeClassName={'active'} href='/teams'
								data-name='main'
								onClick={this.toggleDropdown}
							><i class='fas fa-users' /> <Text id='header.teams'>Teamene</Text></Link>
						</li>
						<li class='nav-item d-block d-lg-none'>
							<Link className='nav-link' activeClassName={'active'} href='/users'
								data-name='main'
								onClick={this.toggleDropdown}
							><i class='fas fa-user' /> <Text id='header.profile'>Din profil</Text></Link>
						</li>
						<li class='nav-item d-block d-lg-none'>
							<Link className='nav-link' activeClassName={'active'} href='/logout'
								data-name='main'
								onClick={this.toggleDropdown}
							><i class='fas fa-sign-out-alt' /> <Text id='header.logout'>Logg ut</Text></Link>
						</li>
						
						{isAdmin && <li class='nav-item d-block d-lg-none'>
							<Link className='nav-link' activeClassName={'active'}
								data-name='main'
								onClick={this.adminLoadAll}
							><i class='fas user-shield' /> Load all</Link>
						</li>}

						<li class='nav-item d-block d-lg-none'>
							<Link className='nav-link' activeClassName={'active'}
								data-name='main'
								onClick={this.toggleDarkmode}
							>
								<i class={`${darkmode ? 'fas text-warning' : 'far'} fa-moon`} /> <Text id='header.darkmode'>Nattmodus</Text>
							</Link>
						</li>

						{isAdmin && allUsers && <div class='d-block d-lg-none' style='height: 40vh; overflow: auto;'>
							{allUsers && allUsers.map(user => (
								<li class='nav-item d-block d-lg-none ml-2 clearfix'>
									<small>
										<Link className='nav-link py-0' activeClassName={'active'}
											data-name='main'
											data-email={user.email}
											onClick={this.simulateUser}
										>
											<div class='float-right d-flex' style='width: 180px;'>
												<div class='flex-fill text-right w-25'><i class='fas fa-users text-muted' /> {user.userTeams.length}</div>
												<div class='flex-fill text-right w-25'><i class='fas fa-dog text-muted' /> {user.userDogs.length}</div>
												<div class='flex-fill text-right w-25'><i class='fas fa-history text-muted' /> {user.dogHistories}</div>
												<div class='flex-fill text-right w-25'><i class='fas fa-running text-muted' /> {user.userWorkouts.length}</div>
											</div>
											<i class='fas fa-user' /> {user.email}
										</Link>
									</small>
								</li>
							))}
						</div>}
					</ul>

					<ul class='navbar-nav ml-auto '>
						<li class='nav-item mr-2 d-none d-lg-block'>
							<button class='btn btn-link'
								data-name='profile'
								onClick={this.toggleDarkmode}
							>
								<i class={`${darkmode ? 'fas text-warning' : 'far'} fa-moon`} />
							</button>
						</li>
						<li class='nav-item ml-auto d-none d-lg-block'>
							<form class='form-inline my-2 my-lg-0'>
								<Localizer>
									<input class='form-control form-control-sm mr-sm-2' type='search' placeholder={<Text id='header.search-placeholder'>Søk etter det meste...</Text>}
										value={searchText}
										onInput={linkState(this, 'searchText')}
									/>
								</Localizer>
								<button class='btn btn-sm btn-outline-success my-2 my-sm-0' type='button'	
									onClick={this.search}
								><Text id='header.search'>Søk</Text></button>
							</form>
						</li>

						<li class='nav-item dropdown d-none d-lg-block'>
							<a class={`${style.pointerCursor} nav-link dropdown-toggle`}
								data-name='profile'
								onClick={this.toggleDropdown}
							>
								<i class='fas fa-user-alt' />
							</a>
							<div class={`dropdown-menu dropdown-menu-right ${menu.profile ? 'show' : ''}`}>
								<Link className='dropdown-item' activeClassName={'active'} href='/users'
									data-name='profile'
									onClick={this.toggleDropdown}
								><Text id='header.profile'>Din profil</Text></Link>
								<div class='dropdown-divider' />
								<Link className='dropdown-item' activeClassName={'active'} href='/logout'
									data-name='profile'
									onClick={this.toggleDropdown}
								><Text id='header.logout'>Logg ut</Text></Link>
							</div>
						</li>
					</ul>

				    

				</div>
			</nav>
		);
	}
}

export default Navigation;
